.fade-in-section {
	opacity: 0;
	transform: translateY(20vh);
	visibility: hidden;
	transition: opacity 1200ms ease-out, transform 600ms ease-out,
		visibility 1200ms ease-out;
	will-change: opacity, transform, visibility;
}
.fade-in-section.is-visible {
	opacity: 1;
	transform: none;
	visibility: visible;
}

.main-body {
	width: 100%;
	height: 101vh;
	position: relative;
	display: flex;
	background-color: white;
}

#main-body img {
	position: absolute;
	left: 0;
	-webkit-transition: opacity 1s ease-in-out;
	-moz-transition: opacity 1s ease-in-out;
	-o-transition: opacity 1s ease-in-out;
	transition: opacity 1s ease-in-out;
}

@keyframes FadeInOut {
	0% {
		opacity: 1;
	}
	45% {
		opacity: 1;
	}
	55% {
		opacity: 0;
	}
	100% {
		opacity: 0;
	}
}

#main-body img.top {
	animation-name: FadeInOut;
	animation-timing-function: ease-in-out;
	animation-iteration-count: infinite;
	animation-duration: 9s;
	animation-direction: alternate;
}

.main-content {
	background-color: #1b2a2f;
}

.main-content2 {
	background: white;
}

.video-content {
	width: 100%;
	height: 100%;
}

.title-box {
	top: 25%;
	position: absolute;
	width: 45%;
	height: 187px;
	margin-left: 20%;
	max-width: 48%;
	min-width: 0;
}

.text-title {
	width: 100%;
	color: #fff;
	font-family: Georgia, 'Times New Roman', Times, serif;
	font-weight: 700;
	font-size: 3.5rem;
	line-height: 1;
	transition: all 0.2s ease-in-out;
	margin: 0;
}

.info-box {
	text-align: center;
	padding: 50px;
}

.text-info {
	width: 100%;
	color: #fff;
	font-family: Georgia, 'Times New Roman', Times, serif;
	font-weight: 600;
	font-size: 35px;
	line-height: 1;
	transition: all 0.2s ease-in-out;
	margin: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.text-title::before {
	content: '';
	position: absolute;
	right: 0;
	top: 0;
	height: 100%;
	background: none;
	animation: leftright 5s linear;
}

.text-sub {
	color: #fff;
	font-family: Georgia, 'Times New Roman', Times, serif;
	font-size: 1.5rem;
	font-weight: 500;
	margin: 0;
}

.flex {
	display: flex;
}

.black {
	color: black;
}

.padding {
	padding: 40px;
}

.bytron-img {
	width: 55%;
	border-radius: 35px;
	box-shadow: -20px 20px 60px;
}

.img-examples {
	width: 40%;
	margin: 10px;
}

.bytron-link {
	color: lightskyblue;
	text-decoration: none !important;
	position: relative;
	display: inline-block;
}
.bytron-link:after {
	background: none repeat scroll 0 0 transparent;
	bottom: 0;
	content: '';
	display: block;
	height: 2px;
	left: 50%;
	position: absolute;
	background: #fff;
	transition: width 0.3s ease 0s, left 0.3s ease 0s;
	width: 0;
}
a:hover:after {
	width: 100%;
	left: 0;
}

@media screen and (max-width: 1024px) {
