.zoom-in-zoom-out {
	width: 100%;
	height: 100%;
	background: rgb(2, 2, 27);
	animation: zoom-in-zoom-out 1s ease-out forwards;
	z-index: 999;
	animation-delay: 2s;
}

@keyframes zoom-in-zoom-out {
	0% {
		transform: scale(1, 1);
		border-radius: 0%;
	}
	10% {
		border-radius: 50%;
	}
	100% {
		transform: scale(0, 0);
		border-radius: 50%;
	}
}
